.button {
  width: 150px;
  height: 30px;
  border-radius: 5px;
  background-color: var(--maincolor);
  border: none;
  color: #ffffff;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  &_main {
      margin-top: 67px;
      width: 300px;
      height: 50px;
      font-size: 16px;
  }
  &_submit {
      width: 100%;
      height: 55px;
      color: #ffffff;
      font-size: 16px;
  }
  &_mini {
      width: 131px;
      height: 40px;
      font-size: 14px;
  }
}
