.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__logo {
      width: 250px;
      img {
          width: 100%;
      }
  }
  &__official {
      color: #ffffff;
      font-size: 12px;
      font-weight: 500;
      text-align: center;
      span {
          display: block;
          font-size: 18px;
      }
  }
  &__phone {
      display: block;
      color: #ffffff;
      font-size: 18px;
      font-weight: 700;
      &:hover {
          color: rgb(233, 24, 24);
          text-decoration: none;
      }
  }
  &__contacts {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
  }
  &__button{
      margin-top: 5px;
  }
}
