.accordion {
  padding: 0;
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 150px;
  border: none;
  outline: none;
  transition: 0.4s;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  font-size: 30px;
  margin-bottom: 15px;
  color: #ffff;
  line-height: 48px;

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .accardion__btn-text {
    z-index: 1;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.accardion__btn-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)

}

.panel {
  padding: 0 18px;
  background-color: white;
  display: none;
  overflow: hidden;

}


.catalog {
  padding-bottom: 100px;
  &__tabs {
      margin-top: 70px;
      list-style-type: none;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 0;
      padding-left: 0;
  }
  &__tab {
      width: 220px;
      height: 50px;
      border: 2px solid #d0d0d0;
      color: #6b6b6b;
      font-size: 18px;
      font-weight: 400;
      text-transform: uppercase;
      text-align: center;
      line-height: 48px;
      transform: skewX(-25deg);
      margin: 0 5px;
      cursor: pointer;
      transition: 0.5s all;
      &:hover {
          box-shadow: 3px 3px 20px var(--maincolor);
          border: none;
      }
      div {
          transform: skewX(25deg);
      }
      &_active {
          color: #fff;
          background-color: var(--maincolor);
          border: none;
      }
  }
  &__content {
      margin-top: 60px;
      display: none;
      justify-content: space-between;
      flex-wrap: wrap;
      &_active {
          display: flex;
      }
  }
  &-item {
      width: 300px;
      min-height: 440px;
      border: 2px solid #e8e8e8;
      padding: 20px 20px 26px 20px;
      overflow: hidden;
      /* margin-bottom: 20px; */
      &__img {
          display: block;
          margin: 0 auto;
          max-width: 100%;
          height: 200px;
      }
      &__subtitle {
          margin-top: 20px;
          color: #0d0d0d;
          font-size: 16px;
          font-weight: 700;
          text-align: center;
      }
      &__descr {
          margin-top: 16px;
          color: #0d0d0d;
          font-size: 14px;
          font-weight: 300;
          text-align: center;
      }
      &__link {
          display: block;
          margin-top: 17px;
          color: var(--maincolor);
          font-size: 14px;
          font-weight: 500;
          text-decoration: underline;
          text-align: center;
          text-transform: uppercase;
          &:hover {
              color: var(--maincolor);
          }
      }
      hr {
          background-color: #e8e8e8;
          /* margin-top: 19px; */
          margin-bottom: 21px;
          border-bottom: none;
      }
      &__footer {
          display: flex;
          justify-content: space-between;
          align-items: center;
      }
      &__old-price {
          color: #555555;
          font-size: 14px;
          font-weight: 300;
          text-decoration: line-through;
      }
      &__price {
          color: #050505;
          font-size: 20px;
          font-weight: 700;
      }
      &__wrapper {
          display: flex;
          width: calc(200% + 40px);
      }
      &__content {
          width: 50%;
          margin-right: 0px;
          transform: translateX(-100%);
          transition: 0.5s all;
          &_active {
              transform: translateX(0%);
              margin-right: 20px;
          }
      }
      &__list {
          position: relative;
          width: 50%;
          margin-left: 20px;
          transform: translateX(0%);
          padding-right: 20px;
          transition: 0.5s all;
          padding-left: 19px;
          margin-bottom: 0;
          list-style-type: none;
          &_active {
              transform: translateX(-100%);
              margin-left: 0px;
          }
          li {
              position: relative;
              color: #555555;
              font-size: 14px;
              font-weight: 300;
              margin-bottom: 20px;
              &:before {
                  content: '';
                  position: absolute;
                  width: 5px;
                  height: 5px;
                  background-color: var(--maincolor);
                  left: -15px;
                  top: 8px;
              }
          }
      }
      &__back {
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translateX(calc(-50% - 19px));
          display: block;
          color: var(--maincolor);
          font-size: 14px;
          font-weight: 500;
          text-decoration: underline;
          text-transform: uppercase;
          &:hover {
              color: var(--maincolor);
          }
      }
  }
}
